.had_verify_avator {
  width: 50px;
  height: 50px;
  margin-right: 5px;
  border-radius: 50%;
  background-color: #F7F7F7;
}
.had_verify_avator > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.had_verify_level {
  padding: 2px;
  border-radius: 4px;
  background-color: #FF0000;
  color: #FFF;
}
.had_verify :global(.am-card-header-extra) {
  flex: initial;
  align-self: flex-start;
}
.had_verify_options {
  display: flex;
  align-items: center;
}
.had_verify_options > div {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.no_verify_info {
  position: relative;
  padding: 20px 15px;
  text-align: center;
}
.no_verify_info > i {
  position: absolute;
  top: 10px;
  right: 15px;
}
.no_verify_info_avator {
  display: inline-block;
  width: 80px;
  height: 80px;
  background-color: #F7F7F7;
  border-radius: 50%;
}
.no_verify_info_avator > img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.operate_item img {
  width: 24px;
  height: 24px;
}
.operate_item .list_item_extra {
  display: inline-block;
  width: 24px;
  height: 24px;
  line-height: 24px;
  text-align: center;
  color: #FFF;
  border-radius: 50%;
  background-color: #FF0000;
}
